import React, { useEffect, useState } from "react"
import list from "../../static/docs/list"

export default function Home() {
  const column = list[0]
  const article = column.list[0]
  const [doc, setDoc] = useState("")
  useEffect(() => {
    import(`../../static/docs/${article.datafile}`).then(doc => {
      setDoc(doc.default)
    })
  })
  return (
    <div className="main-container">
      <h1 className="main-title">{article.title}</h1>
      <div
        dangerouslySetInnerHTML={{
          __html: doc,
        }}
        className="rich-text-wrap"
      ></div>
    </div>
  )
}
